<template>
  <div class="v-index">
    <div class="v-index-title">
      <img src="@/assets/logo.png" />查分助手
    </div>
    <div class="v-index-contanier">
      <van-list v-model="loading" loading-text="正在加载中..." :finished="finished" finished-text="没有更多了" @load="getExam">
        <van-cell-group inset v-for="(item,index) in result" :key="index">
          <van-cell size="large" :title="item.examName"  :label="'考试时间:'+item.time" >
              <template #icon>
                <div class="card_left">
                  <van-icon class="card_icon" name="completed" size="28" />
                  <div>{{item.type}}</div>
                </div>
              </template>
              <template #right-icon>
                <div class="card_right">              
                  <van-icon class="card_icon" name="arrow" size="20"  @click="onIconClick(index)"/>
                </div>
              </template>
          </van-cell>
        </van-cell-group>
      </van-list>
      <div style="height: 1rem;"/>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      result:[],
      index:0,
      nowGet:0,
      rows:5,
      loading:false,
      finished:false,
    };
  },
  mounted(){
    this.index = 0;
    this.nowGet = 0;
    this.result = [];
    this.getExam();
  },
  methods: {
    getExam(){
      console.log("---index:"+this.index + "  list:"+this.result.length)
      let that = this;
      if(this.result.length<this.nowGet)
        return;
      this.nowGet += this.rows;
      console.log("---get exam--")
      this.zone.queryExamList(this.rows,this.index).then(ret =>{
        console.log("---index---");
        console.log(ret);
        if(ret.status == 200){
            if(ret.data.status == 200){
              this.nowGet = ret.data.data.startIndex;
              that.index = ret.data.data.startIndex;
              that.result.push(...ret.data.data.list);
              console.log(this.result)
              that.loading = false;
              if(ret.data.data.list.length == 0)
                that.finished = true;
            }else{
              console.log("---index error---");
              that.$router.push("/login"); 
              that.loading = false;             
            }
        } else {
            console.log("---index error---");
            that.$router.push("/login");
            that.loading = false;
        }
      });
    },
    onIconClick(index){
      console.log("--click--exam--:"+index)
      console.log(this.result[index])
      this.$router.push({path:"/exam",query:this.result[index]});
    }
  },
};
</script>
<style lang="less" scoped>
.v-index-contanier {
  position: absolute;
  top: 51px;
  bottom: 65px;
  width: 100%;
  overflow: auto;
}
.v-index {
  height: 100%;
  background: #f7f7f7;
}
.v-index-title {
  top:0;
  position:fixed;
  background: white;
  border-bottom: 1px solid #eee;
  line-height: 50px;
  height: 50px;
  width:100%;
  padding-left: 1rem;
  letter-spacing: 1px;
  img {
    position: relative;
    height: 24px;
    top: 5px;
    margin-right: 0.5rem;
  }
}
.card-container{
  padding:8px;
  display:flex;
  height:60px;
  background-color: #ffffff;
}
.card_icon{
  display: block;
  align-content: center;
}
.card_left{
  top:20%;
  position:relative;
  width:40px;
  font-size: 10px;
  text-align: center;
  align-content: center;
}
.card_content{
  float:left;
  width:100%;
  font-size: 16px;
  text-align: left;
  .card_name{
    line-height: 16px;
    margin: 0;
  }
}
.card_right{
  padding:20px;
  float:right; 
  width:10px;
}

.card_bottom{
  left:0;
  height:2px;
  background:-webkit-repeating-linear-gradient(135deg,#ff6c6c 0,#ff6c6c 20%,transparent 0,transparent 25%,#1989fa 0,#1989fa 45%,transparent 0,transparent 50%);
  background:repeating-linear-gradient(-45deg,#ff6c6c 0,#ff6c6c 20%,transparent 0,transparent 25%,#1989fa 0,#1989fa 45%,transparent 0,transparent 50%);
  background-size:80px;
  content:''
}
p{
  line-height: 8px;
}
.van-cell-group{
  margin: 10px 10px;
}
</style>


